@font-face {
  font-family: "Roboto";
  font-weight: 400;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 700;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-bold.woff2") format("woff2"), url("../fonts/roboto-bold.woff") format("woff"); }

@font-face {
  font-family: "Roboto";
  font-weight: 500;
  font-style: normal;
  font-display: swap;
  src: url("../fonts/roboto-medium.woff2") format("woff2"), url("../fonts/roboto-medium.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::after,
*::before {
  box-sizing: inherit; }

body {
  margin: 0;
  padding: 0;
  font-size: 12px;
  line-height: 16px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #989691;
  background-color: #e5e5e5; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.container {
  display: flex;
  flex-direction: column;
  width: 100%; }

.container__main {
  min-height: 100%;
  height: 100vh;
  position: relative; }

@media (max-width: 767px) {
  .container__main {
    padding: 0 8px; } }

.flat {
  max-width: 1200px;
  width: 100%;
  box-shadow: 0 2px 24px rgba(0, 0, 0, 0.04);
  border-radius: 8px;
  background-color: #ffffff;
  padding: 2.1% 2.4% 0.8% 2%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: space-between; }
  .flat--hidden {
    display: none; }

.flat__close {
  border: 0;
  background-color: transparent;
  padding: 0;
  cursor: pointer;
  outline: none;
  width: 28px;
  height: 28px;
  position: absolute;
  top: 16px;
  right: 12px;
  transition: background-color 0.4s ease-out 0.1s;
  border-radius: 4px; }
  .flat__close::after, .flat__close::before {
    position: absolute;
    content: "";
    height: 2.1px;
    width: 18.93px;
    background-color: #c4c4c4;
    top: 12px;
    transition: background-color 0.4s ease-out 0.1s; }
  .flat__close::before {
    transform: rotate(-45deg);
    left: 5px; }
  .flat__close::after {
    transform: rotate(45deg);
    right: 4px; }
  .flat__close:hover, .flat__close:focus {
    background-color: #f5f4f2; }
  .flat__close:hover::before, .flat__close:focus::before, .flat__close:hover::after, .flat__close:focus::after {
    background-color: #383736; }
  .flat__close:active {
    background-color: #e7e4de; }

@media (max-width: 1279px) {
  .flat {
    width: 93.91%;
    padding-right: 3.4%;
    padding-top: 2.8%;
    padding-left: 2.5%;
    padding-bottom: 2.4%; } }

@media (max-width: 919px) {
  .flat {
    align-items: flex-start;
    flex-direction: column;
    width: 93%;
    padding: 3.4% 4% 3% 3%; }
  .flat__close {
    right: 15px; } }

@media (max-width: 767px) {
  .flat {
    top: 40px;
    transform: translate(-50%, 0); } }

@media (max-width: 767px) {
  .flat {
    width: 100%;
    padding-left: 6%;
    padding-top: 5.4%;
    padding-right: 6%;
    padding-bottom: 6%;
    position: relative;
    margin: 40px 0;
    left: auto;
    top: auto;
    transform: none; }
  .flat__close {
    right: 14px; } }

.price__choose {
