  transition: background-color 0.4s ease-out 0.1s;
  max-width: 268px;
  width: 100%;
  font-feature-settings: "liga" 0; }
  .price__choose:hover, .price__choose:focus {
    background-color: #f0b505; }
  .price__choose:focus {
    background-color: #e1ab08; }

.price__key-icon {
  flex-shrink: 0;
  margin-right: 8px; }

.price__date {
